@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;700&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
  font-family: 'Montserrat', sans-serif;
}

body {
  font-size: 16px;
  background-color: #f3b431;
  height: 100%;
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 3rem;
}

.container {
  &__tittle {
    color: white;
    text-align: center;
  }
  &__characters {
    margin-top: 2rem;
    width: 90vw;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
  }
  &__character {
    margin-bottom: 1rem;
    flex-basis: 32.8%;
    background: white;
    border-radius: 1rem;
    overflow: hidden;
    padding: 1.5rem;
    cursor: pointer;
    transition: transform 0.2s;

    & p {
      & span {
        font-weight: 700;
      }
      margin-bottom: 1rem;
    }

    &:hover {
      transform: scale(1.03);
      box-shadow: 0px 4px 8px black;
    }

    &-image {
      display: block;
      margin: 1rem auto;
      width: 40%;
      height: 70%;
    }
  }
}
